import React, { useState, useEffect } from "react"
import styled from "styled-components"
import { Link } from "gatsby"
import { useSelector, useDispatch } from 'react-redux'

const Container = styled.div`
  padding: 1.5rem 0;
  display: flex;
  justify-content: space-between;
`

const Wrapper = styled.div`
  display: flex;
`

const Title = styled.h2`
  margin: 0;
  letter-spacing: -0.3px;
  font-weight: 700;
  color: #111111;
  font-size: 1.4rem;

  @media (max-width: 425px) {
    font-size: 1.25rem
  }

  // &:hover {
  //   color: #a5a5a5;
  //   font-weight: 800;
  //   letter-spacing: -0.5px;
  // }
`

const SecondaryTitle = styled(Title)`
  color: #c5c5c5;
  font-weight: 800;
  letter-spacing: -0.5px;
`

const Subtitle = styled.p`
  font-weight: 300;
  font-size: 1.2rem;  
  margin: 0;
  margin-left: 1.5rem;
  padding-left: 1.5rem;
  text-align: bottom;   
  border-left: 1px solid #767676;
  color: #333333;

  @media (max-width: 425px) {
    margin-left: 0.9rem;
    padding-left: 0.9rem;
    font-size: 1rem;
  }

`
const StyledLink = styled(Link)`
  text-decoration: none;
`;

const Menu = styled.a`
  display: none;
  margin: 0;
  letter-spacing: -0.3px;
  font-weight: 400;
  color: #111111;
  font-size: 1.2rem;
  line-height: 1.5;
  cursor: pointer;

  @media (max-width: 425px) {
    font-size: 1rem;
  }

  @media (max-width: 768px) {
    display: block;
  }
`


export default function Header(props) {
  // const [title, setTitle] = useState("John Lim")
  const [hover, setHover] = useState(false)
  const menuState = useSelector(state => state.menuState)
  const dispatch = useDispatch()

  // useEffect(() => {
  //   console.log(menuState);
  // })

  useEffect(() => {
    dispatch({type: "CLOSE"})
  }, [])

  function triggerMenu() {
    if (menuState) {
      dispatch({type: "CLOSE"})
    } else {
      dispatch({type: "OPEN"})
    }
  }

  return (
    <Container>
      <Wrapper>
        <StyledLink 
          to="/"
          onMouseEnter={() => setHover(true)}
          onMouseLeave={() => setHover(false)}
        >
          {
            hover ?
            <SecondaryTitle>Make It Better.</SecondaryTitle> :
            <Title>John Lim</Title>
          }
        </StyledLink>
        {
          props.pageTitle ?
          <>
            <Subtitle>{props.pageTitle}</Subtitle> 
          </>: <></>
        }
      </Wrapper>
      <Menu onClick={() => triggerMenu()}>Menu</Menu>
    </Container>
  )
}